<h1 id="dockerfile" tabindex="-1"><a class="header-anchor" href="#dockerfile" aria-hidden="true">#</a> Dockerfile</h1>
<h2 id="创建dockerfile文件-内容如下" tabindex="-1"><a class="header-anchor" href="#创建dockerfile文件-内容如下" aria-hidden="true">#</a> 创建Dockerfile文件，内容如下：</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>FROM openjdk:8u332-oracle
MAINTAINER Amin Yao xxx@amin.com

COPY target/min-gateway-1.0.0.jar /min-gateway-1.0.0.jar

ENTRYPOINT <span class="token punctuation">[</span><span class="token string">"java"</span>, <span class="token string">"-jar"</span>, <span class="token string">"/min-gateway-1.0.0.jar"</span><span class="token punctuation">]</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h2 id="制作镜像" tabindex="-1"><a class="header-anchor" href="#制作镜像" aria-hidden="true">#</a> 制作镜像</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>docker build -t 镜像名称
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="运行镜像" tabindex="-1"><a class="header-anchor" href="#运行镜像" aria-hidden="true">#</a> 运行镜像</h2>
<p>docker run -t 镜像名称</p>
<h2 id="推送镜像到远程仓库" tabindex="-1"><a class="header-anchor" href="#推送镜像到远程仓库" aria-hidden="true">#</a> 推送镜像到远程仓库</h2>
<h3 id="_1-在本地登录到远程仓库" tabindex="-1"><a class="header-anchor" href="#_1-在本地登录到远程仓库" aria-hidden="true">#</a> 1.在本地登录到远程仓库</h3>
<p>docker login --username=[用户名] -p=[密码]</p>
<p>例：docker login ccr.ccs.tencentyun.com/yaomin/yaomin --username=100010893455</p>
<h3 id="_2-给本地镜像打标签" tabindex="-1"><a class="header-anchor" href="#_2-给本地镜像打标签" aria-hidden="true">#</a> 2.给本地镜像打标签</h3>
<p>docker tag [镜像id] [远程ip:端口/自定义路径/*]:[版本号]</p>
<p>例：docker tag ccr.ccs.tencentyun.com/yaomin/yaomin:1.0</p>
<h3 id="_3-推送" tabindex="-1"><a class="header-anchor" href="#_3-推送" aria-hidden="true">#</a> 3.推送</h3>
<p>docker push [远程ip:端口/自定义路径/*]:[版本号]</p>
<p>例：docker push ccr.ccs.tencentyun.com/yaomin/yaomin:1.0</p>
